.App {
	background-color: #ebe9e4;
	height: 100%;
}
.user-name {
	font-size: 13px;
	font-weight: bold;
	margin: 0;
	padding: 0;
}
.full-name {
	font-size: 14px;
	font-weight: 500;
	color: #a3a096;
	margin: 0;
	padding: 0;
}
.bio {
	font-size: 14px;
	padding: 8px;
}
.nav-bar {
	border-bottom: 1px solid grey;
}
.nav-profile-pic-container {
	height: 28px;
	width: 28px;
	border: 1px solid black;
	border-radius: 50%;
	padding: 1px;
}
.profile-pic {
	border-radius: 50%;
}
.natgram-text {
	font-family: "Pacifico", cursive;
}
.natgram-feed-story {
	border: 1px solid grey;
	border-radius: 4px;
	padding: 8px;
	overflow-x: auto;
}
.story-image {
	border-radius: 50%;
}
.story-added {
	border: 2px solid rgb(168, 4, 4);
}
.story-seen {
	border: 2px solid rgb(228, 216, 216);
}
.story {
	height: 70px;
	width: 70px;
	border-radius: 50%;
	padding: 2px;
}
.story-user-name {
	padding: 0;
	margin: 0 12px 0 0;
	font-size: 12px;
	font-weight: 500;
	color: #a3a096;
}
.natgram-feed-post {
	border: 1px solid grey;
	border-radius: 4px;
}
.feed-post-profile-pic {
	height: 40px;
	width: 40px;
	border: 2px solid rgb(168, 4, 4);
	border-radius: 50%;
	padding: 1px;
}
.feed-post-user-name {
	padding: 0;
	margin: 0 0 0 8px;
	font-size: 13px;
	font-weight: bold;
}
i:hover,
svg:hover {
	cursor: pointer;
}
.like-text {
	margin: -5px 0 5px 0;
	padding: 0;
	font-size: 12px;
}
.profile {
	height: 60px;
	width: 60px;
	border-radius: 50%;
}
.cursor:hover {
	cursor: pointer;
	color: blue;
	font-weight: bold;
}
.feed-post-bottom-section {
	margin: -5px 0 -3px 0;
}
.comment-box:focus {
	outline: none;
}
.comment-box {
	margin-top: -10px;
	color: rgb(13, 46, 105);
}
@media (max-width: 700px) {
	.n13-mt {
		margin-top: -13px;
	}
}
@media (min-width: 1200px) {
	.story-modal-md {
		height: 400px;
	}
}
.n5-mb {
	margin-bottom: -8px;
}
